@use "styles/stylesFunctions.scss" as stylesFunctions;
@use "styles/sassVariables" as v;

.table {
  margin-top: 15px;
  position: relative;
  .removeBtn {
    position: absolute;
    top: 15px;
    left: 15px;
    cursor: pointer;
    width: 20px;
    height: 20px;
    &::after,
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #f44336;
      width: 100%;
      height: 2px;
      border-radius: 1px;
      &:hover {
        background-color: #ff5252;
      }
      &:active {
        background-color: #ff5252;
      }
    }
    &::after {
      transform: rotate(45deg);
    }
    &::before {
      transform: rotate(-45deg);
    }
  }
  .table-header {
    background-color: v.$primary;
    color: #fff;
    font-size: 35px;
    padding: 25px 38px 22px;
  }

  .table-body {
    table {
      border-collapse: collapse;
      width: 100%;
    }
    td,
    th {
      border: 1px solid #ddd;
      padding: 15px;
    }
    tr:ntn-child(even) {
      background-color: ddd;
    }
  }
}

.container {
  margin-top: 50px;
  margin-bottom: 300px;
  h1 {
    font-size: 35px;
    font-weight: bold;
    text-align: center;
  }
}
